import { CLICK_UPDATE_VALUE } from '../actions/actionTypes';
const initialState = {
    newValue: 'Atualizado via Redux!'
};
export const clickReducer = (state = initialState, action: { type: any; newValue: any; }) => {
    switch (action.type) {
        case CLICK_UPDATE_VALUE:
            return {
                ...state,
                newValue: action.newValue
            };
        default:
            return state;
    }
};